.form,
.form-active {
  display: flex;
  font-size: 18px;
  line-height: 24px;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  transition: 0.3s;
  transform: translateX(100%);
}

.form-active {
  transform: translateX(0);
}

.form__side-bar {
  width: 34%;
  background: transparent;
  transition: 0.3s;
}

.form__content {
  width: 70%;
  background: $bodyBackgraund;
}

.form__container {
  padding: 0 16px;
}

.form__header {
  padding-top: 20px;
  margin-bottom: 56px;
}

.form__link-back {
  display: inline-block;
  color: $mainColor;
  position: relative;
  padding-left: 20px;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: calc(50% - 4.5px);
    width: 14px;
    height: 9px;
    background: url('../images/form/arrow-left.svg') no-repeat;
  }
}

.form__content-wrapper {
  display: flex;
}

.form__content-left-bar,
.form__content-right-bar {
  width: 50%;
  max-width: 624px;
}

.form__title {
  font-size: 40px;
  line-height: 44px;
  margin-bottom: 32px;
}

.form__text-left-bar {
  width: 100%;
  max-width: 624px;
}

.form__form-name {
  border-bottom: 1px solid $blackColorOpacity-05;
  padding-bottom: 8px;
  margin-bottom: 16px;
}

.form__form-name-value {
  font-size: 12px;
  line-height: 12px;
  margin-bottom: 4px;
  color: $blackColorOpacity-05;
}

.form__form-input {
  background: transparent;
  font-size: 18px;
  line-height: 24px;
  width: 100%;
}

/////
.form__input-checkbox {
  display: none;
}

.form__checkbox {
  display: inline-block;
  position: relative;
  margin-right: 2px;
  width: 16px;
  height: 16px;
  border: 1px solid $mainColor;
  transform: translateY(1px);

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 16px;
    height: 16px;
    background: url('../images/form/check.svg') center / contain no-repeat;
    transition: opacity 0.2s;
    opacity: 0;
  }
}

.form__input-checkbox:checked ~ .form__checkbox::before {
  opacity: 1;
}

.form__form-politics {
  color: $mainColor;
  border-bottom: 1px solid $mainColor;
  padding-bottom: 1px;
}

.form__response {
  display: block;
  width: 100%;
  max-width: 213px;
  border-radius: 36px;
  background: $whiteColor;
  padding: 8px 40px 8px 16px;
  position: relative;
  font-size: 24px;
  line-height: 24px;
  margin-top: 16px;
  color: $mainColor;

  &::before {
    content: '';
    position: absolute;
    right: 17px;
    top: calc(50% - 4.5px);
    width: 14px;
    height: 9px;
    background: url('../images/form/arrow-right.svg') no-repeat;
  }
}

.form__dropdown {
  position: relative;
  cursor: pointer;
}

.form__dropdown-result {
  position: relative;
  pointer-events: none;

  &::before {
    content: '';
    position: absolute;
    right: 0;
    top: calc(50% - 4.5px);
    width: 9px;
    height: 9px;
    background: url('../images/form/arrow-bottom.svg') no-repeat;
  }
}

.form__dropdown-list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  background: $whiteColor;
  transition: 0.3s;
  padding-top: 8px;
}

.form__dropdown-list-item {
  padding: 4px 8px;
  margin-bottom: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.form__dropdown-item-position {
  pointer-events: none;
}

.form__dropdown-item-checked,
.form__dropdown-item-checked-active {
  width: 10px;
  height: 7.5px;
  transition: 0.2s;
  opacity: 0;
  pointer-events: none;
}

.form__dropdown-item-checked-active {
  opacity: 1;
}

.form__dropdown-item-checked-image {
  display: block;
  width: 100%;
}

.form__file-uploader {
  display: flex;
  margin-bottom: 32px;
}

.form__add-link {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin-right: 16px;
  background: transparent;
}

.form__file-uploader-text {
  margin-bottom: 8px;
}

.form__file-uploader-text-correction {
  font-size: 12px;
  line-height: 12px;
  color: $blackColorOpacity-05;
}

.form__information {
  width: 100%;
  display: block;
  background: transparent;
  font-size: 18px;
  line-height: 24px;
}
